* {
  margin: 0;
  padding: 0;
}
.App {
  height: 100vh;
  background: linear-gradient(160deg, #5E6EFF, #383838);
}

.none {
  display: none;
}

.z-index-top {
  
}
.App {
  text-align: center;
}

.App-logo {
  height: 40vmin;
  pointer-events: none;
}

@media (prefers-reduced-motion: no-preference) {
  .App-logo {
    -webkit-animation: App-logo-spin infinite 20s linear;
            animation: App-logo-spin infinite 20s linear;
  }
}

.App-header {
  background-color: #282c34;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.App-link {
  color: #61dafb;
}

@-webkit-keyframes App-logo-spin {
  from {
    -webkit-transform: rotate(0deg);
            transform: rotate(0deg);
  }
  to {
    -webkit-transform: rotate(360deg);
            transform: rotate(360deg);
  }
}

@keyframes App-logo-spin {
  from {
    -webkit-transform: rotate(0deg);
            transform: rotate(0deg);
  }
  to {
    -webkit-transform: rotate(360deg);
            transform: rotate(360deg);
  }
}

.circle-con {
    width: 100vw;
    height: 0vw;
    position: fixed;
    top: 0;
}

.circle {
    position: absolute;
    border-radius: 365px;
    display: flex;
    align-items: center;
    justify-content: center;
    transition: .1s;
}

.circle h2 {
    font-family: 'Zen Kaku Gothic Antique', sans-serif;
}
.no-nav-h2 {
    font-size: 0px;
    opacity: 0;
    transition: 0s;
    transition-delay: 0s;
}
.show-nav-h2 {
    font-size: 4.5vw;
    opacity: 1;
    transition: .6s;
}


.transition {
    box-shadow: 0 15px 50px rgb(0, 0, 0);
    transition: .6s;
    transition-delay: .1s;
}

.transition:hover {
    padding: 15px;
    cursor: pointer;
    transition: .2s;
    transition-delay: 0;
    box-shadow: 0 0px 50px rgb(251, 255, 147);
    z-index: 10;
}

.transition:hover > h2{
    font-size: 5vw;
    transition: .2s;
}



@media screen and (min-width: 700px) {
    .show-nav-h2 {
       font-size: 30px;
       transition-delay: .1s;
    }

    .circle-con {
        margin-left: -10vw;
    }
    .transition:hover > h2{
        font-size: 39px;
    }

    .no-nav-h2 {
        transition: 0s;
        transition-delay: 0s;
    }
  }


.fa-bars-nav-con {
    width: 100%;
    display: flex;
    justify-content: flex-end;
}

.fa-bars {
    font-size: 25px;
    margin: 10px;
    cursor: pointer;
}

.fa-x {
    position: fixed;
    top: 0;
    left: 0;
    margin: 50px;
    font-size: 25px;
    color: white;
}

.fa-x-open {
    opacity: 1;
    transition: .3s;
    z-index: 999;
    transition-delay: .1s;
    cursor: pointer;
}

.fa-closed {
    opacity: 0;
    z-index: -1;
}


.content {
    height: 100vh;
    width: 100vw;
    position: absolute;
    display: flex;
    justify-content: center;
    align-items: center;
}

.content section {
    background-color: rgba(255, 255, 255, .5);
    border-radius: 10px;
    width: 90%;
    min-height: 90%;
    height: -webkit-fit-content;
    height: -moz-fit-content;
    height: fit-content;
}

.contentOpen {
    opacity: 1;
    transition: .3s;

}

.content-open {
    z-index: 2;
}

.contentClose {
    opacity: 0;
    z-index: 0;
    transition: .3s;
}

